/*
 * AJAX pieprasjumu izpildanas objekts. Objektam var konfigurt ar callback funkciju
 * 
 * @param   url             string    URL darbplsmas iznicializanai
 * @param   form_htm_id     string    Formas HTML objekta identifikators
 * @param   grid_htm_id     string    Reistra tabulr skata HTML objekta identifikators - nepiecieams lai atjaunintu saraksta datus
 * @param   formData        Array     Masvs ar formas POST datiem
 * @return  void
 */
function FormAjaxRequest(url, form_htm_id, grid_htm_id, formData) {
    this.url = url;
    this.form_htm_id = form_htm_id;
    this.grid_htm_id = grid_htm_id;
    this.formData = formData;

    this.progress_info = DX_CORE.trans_data_processing;
    this.callback = null;
    this.err_callback = null;

    this.doRequest = function () {

        if (this.form_htm_id)
        {
            if (is_executing(this.form_htm_id))
            {
                return;
            }

            start_executing(this.form_htm_id);
        }

        // vlreiz jdefin, lai ie maingie ir pieejami ajax funkcijs, jo tajs "this" ir jau cits objekts
        var callback = this.callback;
        var form_htm_id = this.form_htm_id;
        var progress_info = this.progress_info;
        var err_callback = this.err_callback;

        $.ajax({
            type: 'POST',
            url: DX_CORE.site_url + this.url,
            data: this.formData,
            processData: false,
            contentType: false,
            dataType: "json",
            async: true,
            success: function (data) {
                try
                {
                    if (data["success"] === 1)
                    {
                        callback.call(this, data);
                        hide_page_splash();
                        hide_form_splash();
                    } else
                    {
                        notify_err(data["error"]);
                        hide_page_splash(1);
                        hide_form_splash(1);

                        if (err_callback)
                        {
                            err_callback.call(this, data["error"]);
                        }
                    }
                } catch (err)
                {
                    notify_err(escapeHtml(err));
                    hide_page_splash(1);
                    hide_form_splash(1);
                    if (err_callback)
                    {
                        err_callback.call(this, escapeHtml(err));
                    }
                }

                if (form_htm_id)
                {
                    stop_executing(form_htm_id);
                }

            },
            beforeSend: function () {
                if (progress_info) {
                    show_page_splash();
                    show_form_splash();
                }
            },
            error: function (jqXHR, textStatus, errorThrown)
            {
                var err_txt = PageMain.getAjaxErrTxt(jqXHR);
                if (err_callback)
                {
                    err_callback.call(this, err_txt);
                }
                console.log("AJAX kda: " + errorThrown);
                if (form_htm_id)
                {
                    stop_executing(form_htm_id);
                }
            }
        });
    };
}

/*
 * AJAX pieprasjumu izpildanas objekts (IE9 atbalsts). Objektam var konfigurt ar callback funkciju
 * Funkciju paredzts izsaukt no publisk portla nevis no SVS
 * Atsirba - ai funkcijai nevar padot FormData, bet gan tikai tekstu, kur ar & atdalti parametri, piemram, p1=vertiba1&p2=vertiba2
 * 
 * @param   url             string    URL darbplsmas iznicializanai
 * @param   form_htm_id     string    Formas HTML objekta identifikators
 * @param   grid_htm_id     string    Reistra tabulr skata HTML objekta identifikators - nepiecieams lai atjaunintu saraksta datus
 * @param   formData        string    Ar & atdalti parametri un to vrtbas teksta veid
 * @return  void
 */
function FormAjaxRequestIE9(url, form_htm_id, grid_htm_id, formData) {
    this.url = url;
    this.form_htm_id = form_htm_id;
    this.grid_htm_id = grid_htm_id;
    this.formData = formData;

    this.progress_info = DX_CORE.trans_data_processing;
    this.callback = null;
    this.err_callback = null;

    this.doRequest = function () {

        if (this.form_htm_id)
        {
            if (is_executing(this.form_htm_id))
            {
                return;
            }

            start_executing(this.form_htm_id);
        }

        // vlreiz jdefin, lai ie maingie ir pieejami ajax funkcijs, jo tajs "this" ir jau cits objekts
        var callback = this.callback;
        var form_htm_id = this.form_htm_id;
        var progress_info = this.progress_info;
        var err_callback = this.err_callback;

        $.ajax({
            type: 'POST',
            url: DX_CORE.site_url + this.url,
            data: this.formData,
            async: true,
            success: function (data) {
                try
                {
                    if (data["success"] === 1)
                    {
                        callback.call(this, data);
                    } else
                    {
                        notify_err(data["error"]);
                        if (err_callback)
                        {
                            err_callback.call(this, data["error"]);
                        }
                    }
                } catch (err)
                {
                    notify_err(escapeHtml(err));
                    if (err_callback)
                    {
                        err_callback.call(this, escapeHtml(err));
                    }
                }

                if (form_htm_id)
                {
                    stop_executing(form_htm_id);
                }
            },
            beforeSend: function () {
                if (progress_info != "") {
                    show_dx_progres(progress_info);
                    show_form_splash();
                }
            },
            complete: function () {
                if (progress_info != "") {
                    hide_dx_progres();
                    hide_form_splash();
                }
            },
            error: function (jqXHR, textStatus, errorThrown)
            {
                var err_txt = PageMain.getAjaxErrTxt(jqXHR);
                if (err_callback)
                {
                    err_callback.call(this, err_txt);
                }
                console.log("AJAX kda: " + errorThrown);
                if (form_htm_id)
                {
                    stop_executing(form_htm_id);
                }
            }
        });
    };
}

