/**
 * Atgrie elementa vrtbu. Ja elements nav atrasts, tad noklusto vrtbu
 * 
 * @param {Object} parent_elem Veckais elements, kur tiks veikta meklana
 * @param {string} selector    Mekljamais elements
 * @param {mixed} default_val  Noklust vrtba
 * @returns {mixed}
 */
function getElementVal(parent_elem, selector, default_val) {
    var elem = parent_elem.find(selector);

    if (!elem.length)
    {
        return default_val;
    }

    return elem.val();
}
;

var debug_first_log = 0;
function debug_log(txt) {
    var d = new Date();
    var n = d.getTime();

    if (debug_first_log == 0) {
        debug_first_log = n;
    }

    var tm = n - debug_first_log;
    console.log("Miliseconds: " + tm + ": " + txt);
}