/**
 * Atrasto darbinieku saiu JavaScript funkcionalitte
 * 
 * @type _L4.Anonym$0|Function
 */
var EmployeesLinks = function()
{ 
    /**
     * Funkcija, kas notra meklanas lauku vrtbas
     * Funkcija tiek uzstdta pie meklanas rku inicializanas
     * 
     * @type Function
     */
    var clearFields = null;
    
    /**
     * POST formas URL
     * 
     * @type String
     */
    var post_url = "";

    /**
     * Nosta formas datus uz serveri - izpilda lapas prldi ar GET parametriem
     * Ja nepiecieams, tad nomaina formas URL (action atribts)
     * 
     * @param {string} url_params GET pieprasjuma parametri
     * @returns {undefined}
     */
    var submitForm = function(url_params) {
        show_page_splash();
        url_params = url_params + "&is_from_link=1";
        window.location.href = DX_CORE.site_url + "search?" + encodeURI(url_params);
    };
    
    /**
     * Nodroina struktrvienbas saites funkcionalitti - atlasa darbiniekus, kuriem tda pati struktrvienba
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var handleLinkDepartment = function(block_elem) {

        block_elem.find('a.dx_department_link').click(function(e) {
            e.preventDefault();
            var url_params = "department_id=" + $(this).attr('dx_dep_id') + "&source_id=" + $(this).attr('dx_source_id');
            submitForm(url_params);
        });
    };

    /**
     * Nodroina amata saites funkcionalitti - atlasa darbiniekuks, kuriem tds pats amats
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var handleLinkPosition = function(block_elem) {
        block_elem.find('a.dx_position_link').click(function(e) {
            e.preventDefault();
            
            var url_params = "position=" + $(this).attr('dx_attr') + "&source_id=" + $(this).attr('dx_source_id');
            submitForm(url_params);
        });
    };

    /**
     * Nodroina kabineta saites funkcionalitti - atlasa darbiniekus, kuriem tds pats kabinets un adrese
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var handleLinkCabinet = function(block_elem) {
        block_elem.find('a.dx_cabinet_link').click(function(e) {
            e.preventDefault();
            
            var url_params = "cabinet=" + $(this).attr('dx_attr') + "&office_address=" + $(this).attr('dx_office_address');
            submitForm(url_params);
        });
    };

    /**
     * Nodroina tie vadtja saites funkcionalitti - atlasa darbiniekus, kuriem tds pats tieais vadtjs k ar pau tieo vadtju
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var handleLinkManager = function(block_elem) {
        block_elem.find('a.dx_manager_link').click(function(e) {
            e.preventDefault();

            var url_params = "manager_id=" + $(this).attr('dx_manager_id');
            submitForm(url_params);
        });
    };

    /**
     * Nodroina aizvietotja saites funkcionalitti - atlasa darbinieka aizvietotju
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var handleLinkSubstit = function(block_elem) {
        block_elem.find('a.dx_substit_link').click(function(e) {
            e.preventDefault();
            
            var url_params = "subst_empl_id=" + $(this).attr('dx_subst_empl_id');
            submitForm(url_params);
        });
    };
    
    /**
     * Handles button "Open profile" click event
     * 
     * @param {object} block_elem Employees page HTML element
     * @returns {undefined}
     */
    var handleOpenProfile = function(block_elem) {
        block_elem.find('a.dx-open-profile').click(function(e) {
            e.preventDefault();
            
            if (block_elem.attr("data-profile-url").length > 0) {
                // open public profile
                window.location.href = DX_CORE.site_url + block_elem.attr("data-profile-url") + "?empl_id=" + $(this).attr("data-empl-id");
            }
            else {
                // open HR profile
                view_list_item("form", $(this).attr("data-empl-id"), block_elem.attr("data-employees-list-id"), 0, 0, "", ""); 
            }
        });
    };
    
    /**
     * Uzstda POST formas URL
     * 
     * @param {string} url Formas URL
     * @returns {undefined}
     */
    var initFormUrl = function(url) {
        post_url = url;
    };
    
    /**
     * Uzstda meklanas rezulttos attloto saiu funkcionalitti
     * Nospieot uz saitm, tiek veikta darbinieku meklana - piemram, visi tie vadtja pakautie darbinieki, vai visi darbinieki, kas ir no viena kabineta utt
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @returns {undefined}
     */
    var initLinksHandles = function(block_elem) {        
        handleLinkDepartment(block_elem);
        handleLinkPosition(block_elem);
        handleLinkCabinet(block_elem);
        handleLinkManager(block_elem);
        handleLinkSubstit(block_elem);
        handleOpenProfile(block_elem);
    };
    
    /**
     * Inicializ darbinieku lapas JavaScript funkcionalitti
     * 
     * @param {Object} block_elem jQuery objekts HTML elementam, kur tiek ieldta darbinieku meklanas funkcionalitte
     * @param {Function} callback_clearFields Uzstda funkciju, kura notra meklanas laukus
     * @returns {undefined}
     */
    var initLinks = function(block_elem, callback_clearFields) {        
        
        clearFields = callback_clearFields;
        
        initLinksHandles(block_elem);
    };

   

    return {
        init: function(block_elem, callback_clearFields) {
            initLinks(block_elem, callback_clearFields);
        },
        setFormUrl: function(url) {
            initFormUrl(url);
        }
    };
}();